import { Component, OnInit } from '@angular/core';
import {Administrator} from '../../entity';
import {ActivatedRoute} from '@angular/router';
import {AdministratorService} from '../../administrator.service';
import {Location} from '@angular/common';

@Component({
  selector: 'app-add',
  templateUrl: './administrator-add.component.html',
  styleUrls: ['./administrator-add.component.css']
})
export class AdministratorAddComponent implements OnInit {
  administrator: Administrator;
  constructor(
    private route: ActivatedRoute,
    private administratorService: AdministratorService,
    private location: Location,
  ) { }

  ngOnInit() {
  }
  save(no: string, name: string, phone: string): void {
    // tslint:disable-next-line:max-line-length
    this.administrator = {id: null, no: no.trim(),  name: name.trim(), phone: phone.trim()};
    if (!no || !name || !phone) {
      alert('请将信息填写完整');
      return;
    }
    this.administratorService.addAdministrator(this.administrator)
      .subscribe(res => {alert(res.message); this.goBack(); });
  }

  goBack(): void {
    this.location.back();
  }

}
