import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import {LogoutService} from '../logout.service';

@Component({
  selector: 'app-student-user',
  templateUrl: './student-user.component.html',
  styleUrls: ['./student-user.component.css']
})
export class StudentUserComponent implements OnInit {
  private studentUsername = window.localStorage.getItem('studentUsername');
  // tslint:disable-next-line:variable-name
  private student_name = window.localStorage.getItem('student_name');
  // tslint:disable-next-line:variable-name
  private student_user_id = window.localStorage.getItem('student_user_id');
  private studentLoginTime = window.localStorage.getItem('studentLoginTime');

  constructor(private router: Router, private logoutService: LogoutService) {
  }

  ngOnInit() {
  }

  logout() {
    // tslint:disable-next-line:variable-name
    const confirm_ = confirm('确认退出?');
    if (confirm_) {
      this.logoutService.logout().subscribe(res => {
        this.router.navigateByUrl('/');
      });
    } else {
      return;
    }
  }
}
