import {Component, OnInit} from '@angular/core';
import {Teacher, Degree, Department, ProfTitle, School} from '../../entity';
import {ActivatedRoute} from '@angular/router';
import {TeacherService} from '../../teacher.service';
import {DegreeService} from '../../degree.service';
import {Location} from '@angular/common';
import {DepartmentService} from '../../department.service';
import {ProfTitleService} from '../../profTitle.service';
import {SchoolService} from '../../school.service';

@Component({
  selector: 'app-add',
  templateUrl: './teacher-add.component.html',
  styleUrls: ['./teacher-add.component.css']
})
export class TeacherAddComponent implements OnInit {
  degrees: Degree[];
  departments: Department[];
  profTitles: ProfTitle[];
  schools: School[];
  teacher: Teacher;
  title: ProfTitle;
  degree: Degree;
  department: Department;
  school: School;
  constructor(
    private route: ActivatedRoute,
    private teacherService: TeacherService,
    private degreeService: DegreeService,
    private departmentService: DepartmentService,
    private profTitleService: ProfTitleService,
    private schoolService: SchoolService,
    private location: Location,
  ) { }

  ngOnInit() {
    this.getDegrees() ;
    this.getProfTitles();
    this.getDepartments();
    this.getSchools();
  }
  getSchools(): void {
    this.schoolService.getSchools ()
      .subscribe(res => this.schools = res);
  }
  getDegrees(): void {
    this.degreeService.getDegrees ()
      .subscribe(res => this.degrees = res);
  }
  getDepartments(): void {
    this.departmentService.getDepartments ()
      .subscribe(res => this.departments = res);
  }
  getDepartmentBySchool(id: number): void {
    this.schoolService.getDepartmentBySchool(id)
      .subscribe(res => this.departments = res);
  }
  getProfTitles(): void {
    this.profTitleService.getProfTitles ()
      .subscribe(res => this.profTitles = res);
  }
  // @ts-ignore
  compareFn(o1: Compare, o2: Compare): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }
  save(no: string, phone: string, name: string, title: ProfTitle, degree: Degree, department: Department): void {
    // tslint:disable-next-line:max-line-length
    this.teacher = {id: null, no: no.trim(), phone: phone.trim(), name: name.trim(), title: this.title, degree: this.degree, department: this.department};
    if (!name || !title || !degree || !department) { alert('请将信息填写完整!'); return; }
    this.teacherService.addTeacher(this.teacher)
      .subscribe(res => {alert(res.message); this.goBack(); });
  }

  goBack(): void {
    this.location.back();
  }

}
