import {Component, DoCheck, OnInit} from '@angular/core';
import {Elective} from '../../entity';
import {ElectiveService} from '../../elective.service';
import {ActivatedRoute} from '@angular/router';
import {Location} from '@angular/common';

@Component({
  selector: 'app-list-student',
  templateUrl: './list-student.component.html',
  styleUrls: ['./list-student.component.css']
})
export class ListStudentComponent implements OnInit, DoCheck {
  private electives: Elective[];
  private dataShow: Elective[]; // 分页后在当前页要显示的数据
  public leng: number;                 // 数据总量，需要传给子组件的变量
  public currentPag: number;           // 保存从子组件中传来的当前显示页面
  public pagNums: number;              // 保存从子组件中传来的每页显示数量
  public oldCurrentPag: number;
  public oldPagNums: number;

  constructor(
    private route: ActivatedRoute,
    private elective: ElectiveService,
    private location: Location
  ) {
  }

  ngOnInit() {
    const id = this.route.snapshot.params.id;
    this.elective.getElectivesByCourse(id)
      .subscribe(res => {
        this.electives = res;
        this.leng = res.length;
        if (this.leng === 0) {
          alert('还未有学生选择此课程!');
        }
      });
  }

// DoCheck 触发变更检测机制就是调用DoCheck钩子，目的是保证组件属性和浏览器的显示同步
  ngDoCheck(): void {
    if (this.currentPag !== this.oldCurrentPag || this.pagNums !== this.oldPagNums) {
      this.dataShow = [];
      let startNum = (this.currentPag - 1) * this.pagNums;
      for (let i = 0; i < this.pagNums; i++, startNum++) {
        if (this.electives[startNum] !== null && this.electives[startNum] !== undefined) {
          this.dataShow.push(this.electives[startNum]);
        }
      }
      this.oldCurrentPag = this.currentPag;
      this.oldPagNums = this.pagNums;
    }
  }

// 捕获当前显示页码函数
  currentPagHandel(pag: number) {
    this.currentPag = pag;
  }

// 捕获每页显示数量行数
  pagNumsHandel(pagNum: number) {
    this.pagNums = pagNum;
  }

  goBack(): void {
    this.location.back();
  }

  getStudentByNo(no: string): void {
    if (!no) {
      alert('请填入查询信息!');
    } else {
      // tslint:disable-next-line:prefer-for-of
      for (let i = 0; i < this.electives.length; i++) {
        if (this.electives[i].student.no === no) {
          this.dataShow = [];
          this.dataShow.push(this.electives[i]);
          this.leng = this.electives.length;
          this.currentPag = 1;
        }
      }
      if (this.dataShow[0] === undefined || this.dataShow[0].student.no !== no) {
        alert('未查询到相关学生!');
      }
    }
  }

  fresh(): void {
    window.location.reload();
  }

}
