import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {Location} from '@angular/common';
import {CourseService} from '../../course.service';
import {Course, Teacher} from '../../entity';
import {TeacherService} from '../../teacher.service';

@Component({
  selector: 'app-update',
  templateUrl: './course-update.component.html',
  styleUrls: ['./course-update.component.css']
})
export class CourseUpdateComponent implements OnInit {
  course: Course;
  teachers: Teacher[];
  protected config: any = {
    uiColor: '#F8F8F8',   // 编辑框背景色
    language: 'zh-cn',  // 显示语言
    toolbarCanCollapse: true,  // 是否可收缩功能栏
    // tslint:disable-next-line:max-line-length
    toolbar: [['Maximize'], ['Undo', 'Redo', '-', 'Cut', ' Copy', 'Paste', 'PasteText', 'PasteFromWord', '-', 'Link', 'Unlink', 'Anchor', '-', 'Image', 'Table', 'HorizontalRule', 'Smiley', 'SpecialChar', '-', 'Source'], ['Bold', 'Italic', 'Underline', 'Strike', '-', 'Subscript', 'Superscript', '-', 'NumberedList', 'BulletedList', '-', 'Outdent', 'Indent', 'Blockquote'], ['Styles', 'Format', 'Font', 'FontSize']]  // 工具部分
  };

  constructor(
    private route: ActivatedRoute,
    private teacherService: TeacherService,
    private courseService: CourseService,
    private location: Location
  ) {
  }

  ngOnInit() {
    this.getTeachers();
    const id = this.route.snapshot.params.id;
    this.courseService.getCourse(id)
      .subscribe(res => this.course = res);
  }

  getTeachers(): void {
    this.teacherService.getTeachers()
      .subscribe(res => this.teachers = res);
  }

  save(): void {
    this.courseService.updateCourse(this.course)
      .subscribe((res) => {
        alert(res.message);
        this.goBack();
      });
  }

  goBack(): void {
    this.location.back();
  }


  // @ts-ignore
  compareFn(o1: Compare, o2: Compare): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }

}
