import {Component, OnInit} from '@angular/core';
import {Student} from '../../entity';
import {ActivatedRoute, Router} from '@angular/router';
import {StudentService} from '../../student.service';
import {Location} from '@angular/common';

@Component({
  selector: 'app-student-person',
  templateUrl: './student-person.component.html',
  styleUrls: ['./student-person.component.css']
})
export class StudentPersonComponent implements OnInit {
  student: Student;
  // tslint:disable-next-line:variable-name
  private student_user_id = window.localStorage.getItem('student_user_id');

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private studentService: StudentService,
    private location: Location
  ) {
  }

  ngOnInit() {
    // tslint:disable-next-line:variable-name
    const student_id = window.localStorage.getItem('student_id');
    if (student_id === null) {
      alert('请登录或重新登录!');
      this.router.navigateByUrl('/');
    } else {
      this.studentService.getStudent(student_id)
        .subscribe((res) => {
          this.student = res;
        });
    }
  }

  goBack(): void {
    this.location.back();
  }

}
