import {Injectable} from '@angular/core';
import {User, Response} from './entity';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable, of} from 'rxjs';
import {catchError} from 'rxjs/operators';

const httpOptions = {
  headers: new HttpHeaders({'Content-Type': 'application/json'})
};

@Injectable({
  providedIn: 'root'
})
export class LoginService {
  response: Response;
  private loginUrl = '../login';

  constructor(private http: HttpClient) {
  }

  /** POST: add a new user to the server */
  checkTeacherUser(user: { username: string, password: string }): Observable<any> {
    return this.http.post<any>(this.loginUrl + '?paraType=teacher', user);
  }
  /** POST: add a new user to the server */
  checkStudentUser(user: { username: string, password: string }): Observable<any> {
    return this.http.post<any>(this.loginUrl + '?paraType=student', user);
  }
  /** POST: add a new user to the server */
  checkAdministratorUser(user: { username: string, password: string }): Observable<any> {
    return this.http.post<any>(this.loginUrl + '?paraType=administrator', user);
  }
}
