import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import {LogoutService} from '../logout.service';

@Component({
  selector: 'app-administrator-user',
  templateUrl: './administrator-user.component.html',
  styleUrls: ['./administrator-user.component.css']
})
export class AdministratorUserComponent implements OnInit {
  private administratorUsername = window.localStorage.getItem('administratorUsername');
  // tslint:disable-next-line:variable-name
  private administrator_name = window.localStorage.getItem('administrator_name');
  // tslint:disable-next-line:variable-name
  private administrator_user_id = window.localStorage.getItem('administrator_user_id');
  private administratorLoginTime = window.localStorage.getItem('administratorLoginTime');

  constructor(private router: Router, private logoutService: LogoutService) {
  }

  ngOnInit() {
  }

  logout() {
    // tslint:disable-next-line:variable-name
    const confirm_ = confirm('确认退出?');
    if (confirm_) {
      this.logoutService.logout().subscribe(res => {
        this.router.navigateByUrl('/');
      });
    } else {
      return;
    }
  }
}
