import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Administrator, Response} from './entity';
import {Observable, of} from 'rxjs';
import {catchError} from 'rxjs/operators';

const httpOptions = {
  headers: new HttpHeaders({'Content-Type': 'application/json'})
};

@Injectable({
  providedIn: 'root'
})
export class AdministratorService {
  response: Response;
  private administratorUrl = '../administrator.ctl';

  constructor(private http: HttpClient) {
  }

  /** GET administrators from the server */
  getAdministrators(): Observable<Administrator[]> {
    return this.http.get<Administrator[]>(this.administratorUrl)
      .pipe(
        catchError(this.handleError<Administrator[]>('getAdministrators', []))
      );
  }

  /** GET administrators from the server */
  getAdministratorsByNo(no: string): Observable<Administrator[]> {
    return this.http.get<Administrator[]>(this.administratorUrl + '?no=' + no)
      .pipe(
        catchError(this.handleError<Administrator[]>('getAdministrators no=${no}', []))
      );
  }

  /** GET administrator by id. Will 404 if id not found */
  getAdministrator(id: number | string): Observable<Administrator> {
    const url = this.administratorUrl + '?id=' + id;
    return this.http.get<Administrator>(url).pipe(
      catchError(this.handleError<Administrator>(`getAdministrator id=${id}`))
    );
  }

  /** POST: add a new administrator to the server */
  addAdministrator(administrator: Administrator): Observable<Response> {
    return this.http.post<Response>(this.administratorUrl, administrator, httpOptions).pipe(
      catchError(this.handleError<any>('addAdministrator'))
    );
  }

  /** DELETE: detail the administrator from the server */
  deleteAdministrator(administrator: Administrator | number): Observable<Response> {
    const id = typeof administrator === 'number' ? administrator : administrator.id;
    return this.http.delete<Response>(this.administratorUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<Response>('deleteAdministrator'))
    );
  }

  /** PUT: update the hero on the server */
  updateAdministrator(administrator: Administrator): Observable<Response> {
    return this.http.put<Response>(this.administratorUrl, administrator, httpOptions).pipe(
      catchError(this.handleError<Response>('updateAdministrator'))
    );
  }

  /**
   * Handle Http operation that failed.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error); // log to console instead
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
