import {Component, OnInit} from '@angular/core';
import {Grade, Department, School, Student} from '../../entity';
import {ActivatedRoute} from '@angular/router';
import {StudentService} from '../../student.service';
import {Location} from '@angular/common';
import {DepartmentService} from '../../department.service';
import {SchoolService} from '../../school.service';
import {GradeService} from '../../grade.service';


@Component({
  selector: 'app-update',
  templateUrl: './student-update.component.html',
  styleUrls: ['./student-update.component.css']
})
export class StudentUpdateComponent implements OnInit {
  student: Student;
  grades: Grade[];
  schools: School[];
  departments: Department[];
  department: Department;
  grade: Grade;

  constructor(
    private route: ActivatedRoute,
    private studentService: StudentService,
    private departmentService: DepartmentService,
    private schoolService: SchoolService,
    private gradeService: GradeService,
    private location: Location
  ) {
  }

  // @ts-ignore
  compareFn(o1: Compare, o2: Compare): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }

  ngOnInit() {
    this.getSchools();
    this.getDepartments();
    this.getGrades();
    const id = this.route.snapshot.params.id;
    this.studentService.getStudent(id)
      .subscribe(res => this.student = res);
  }

  getSchools(): void {
    this.schoolService.getSchools()
      .subscribe(res => this.schools = res);
  }

  getDepartments(): void {
    this.departmentService.getDepartments ()
      .subscribe(res => this.departments = res);
  }

  getGrades(): void {
    this.gradeService.getGrades()
      .subscribe(res => this.grades = res);
  }

  getDepartmentBySchool(id: number): void {
    this.schoolService.getDepartmentBySchool(id)
      .subscribe(res => this.departments = res);
  }

  getGradeByDepartment(id: number): void {
    this.departmentService.getGradeByDepartment(id)
      .subscribe(res => this.grades = res);
  }

  goBack(): void {
    this.location.back();
  }

  save(): void {
    this.studentService.updateStudent(this.student)
      .subscribe(res => {
        alert(res.message);
        this.goBack();
      });
  }
}
