import {Injectable} from '@angular/core';
import {Course, Response} from './entity';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable, of} from 'rxjs';
import {catchError} from 'rxjs/operators';

const httpOptions = {
  headers: new HttpHeaders({'Content-Type': 'application/json'})
};

@Injectable({
  providedIn: 'root'
})
export class CourseService {
  response: Response;
  private courseUrl = '../course.ctl';

  constructor(private http: HttpClient) {
  }

  /** GET courses from the server */
  getCourses(): Observable<Course[]> {
    return this.http.get<Course[]>(this.courseUrl)
      .pipe(
        catchError(this.handleError<Course[]>('getCourses', []))
      );
  }

  /** GET course by id. Will 404 if id not found */
  getCourse(id: number): Observable<Course> {
    const url = this.courseUrl + '?id=' + id;
    return this.http.get<Course>(url).pipe(
      catchError(this.handleError<Course>(`getCourse id=${id}`))
    );
  }

  /** GET course by id. Will 404 if id not found */
  // tslint:disable-next-line:variable-name
  getCoursesByTeacher(teacher_id: number | string): Observable<Course[]> {
    const url = this.courseUrl + '?teacher_id=' + teacher_id;
    return this.http.get<Course[]>(url)
      .pipe(
      catchError(this.handleError<Course[]>(`getCourseByTeacher teacher_id=${teacher_id}`))
    );
  }

  getCoursesByDes(des: string): Observable<Course[]> {
    const url = this.courseUrl + '?des=' + des;
    return this.http.get<Course[]>(url)
      .pipe(
        catchError(this.handleError<Course[]>(`getCoursesByDes des=${des}`))
      );
  }

  /** POST: add a new course to the server */
  addCourse(course: Course): Observable<Response> {
    return this.http.post<Response>(this.courseUrl, course, httpOptions).pipe(
      catchError(this.handleError<any>('addCourse'))
    );
  }

  /** DELETE: detail the course from the server */
  deleteCourse(course: Course | number): Observable<Response> {
    const id = typeof course === 'number' ? course : course.id;
    return this.http.delete<Response>(this.courseUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<Response>('deleteCourse'))
    );
  }

  /** PUT: update the hero on the server */
  updateCourse(course: Course): Observable<Response> {
    return this.http.put<Response>(this.courseUrl, course, httpOptions).pipe(
      catchError(this.handleError<Response>('updateCourse'))
    );
  }

  /**
   * Handle Http operation that failed.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error); // log to console instead
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
