import {Component, OnInit} from '@angular/core';
import {Teacher, User} from '../../entity';
import {UserService} from '../../user.service';
import {Location} from '@angular/common';
import {Router} from '@angular/router';
import {TeacherService} from '../../teacher.service';

@Component({
  selector: 'app-change-teacher',
  templateUrl: './change-teacher.component.html',
  styleUrls: ['./change-teacher.component.css']
})
export class ChangeTeacherComponent implements OnInit {
  // tslint:disable-next-line:variable-name
  private teacher_user_id = window.localStorage.getItem('teacher_user_id');
  private teacherPassword: string;
  user: User;
  teacher: Teacher;

  constructor(private userService: UserService,
              private teacherService: TeacherService,
              private router: Router,
              private location: Location) {
  }

  ngOnInit() {
    // tslint:disable-next-line:variable-name
    const teacher_id = window.localStorage.getItem('teacher_id');
    this.teacherService.getTeacher(teacher_id)
      .subscribe(res => this.teacher = res);

    if (teacher_id === null) {
      alert('请登录或重新登录!');
      this.router.navigateByUrl('/');
    } else {
      this.userService.getUserTeacherById(this.teacher_user_id)
        .subscribe(res => this.user = res);
    }
  }

  goBack(): void {
    this.location.back();
  }

  save(oldPassword: string, newPassword: string, renewPassword: string): void {
    this.userService.getUserTeacherById(this.teacher_user_id)
      .subscribe((res) => this.teacherPassword = res.password);
    if (!oldPassword || !newPassword || !renewPassword) {
      alert('😮请输入旧密码和新密码!😮');
      return;
    } else if (oldPassword !== this.teacherPassword) {
      alert('😮您输入的旧密码不正确!😮');
      return;
    } else if (newPassword !== renewPassword) {
      alert('😮您两次输入的新密码不一致!😮');
      return;
    } else {
      this.user.password = renewPassword;
      this.userService.changeTeacherPassWord(this.user)
        .subscribe(res => {
          alert(res.message);
          this.goBack();
        });
    }
  }

  save2(): void {
    this.teacherService.updateTeacher(this.teacher)
      .subscribe(res => {
        alert(res.message);
        this.goBack();
      });
  }
}
