import { Injectable } from '@angular/core';
import {Student, Response} from './entity';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable, of} from 'rxjs';
import {catchError} from 'rxjs/operators';

const httpOptions = {
  headers: new HttpHeaders({'Content-Type': 'application/json'})
};

@Injectable({
  providedIn: 'root'
})
export class StudentService {
  response: Response;
  private studentUrl = '../student.ctl';

  constructor(private http: HttpClient) {
  }

  /** GET students from the server */
  getStudents(): Observable<Student[]> {
    return this.http.get<Student[]>(this.studentUrl)
      .pipe(
        catchError(this.handleError<Student[]>('getStudents', []))
      );
  }

  /** GET students from the server */
  getStudentsByNo(no: string): Observable<Student[]> {
    return this.http.get<Student[]>(this.studentUrl + '?no=' + no)
      .pipe(
        catchError(this.handleError<Student[]>('getStudents no=${no}', []))
      );
  }

  /** GET student by id. Will 404 if id not found */
  getStudent(id: number | string): Observable<Student> {
    const url = this.studentUrl + '?id=' + id;
    return this.http.get<Student>(url).pipe(
      catchError(this.handleError<Student>(`getStudent id=${id}`))
    );
  }

  /** POST: add a new student to the server */
  addStudent(student: Student): Observable<Response> {
    return this.http.post<Response>(this.studentUrl, student, httpOptions).pipe(
      catchError(this.handleError<any>('addStudent'))
    );
  }

  /** DELETE: detail the student from the server */
  deleteStudent(student: Student | number): Observable<Response> {
    const id = typeof student === 'number' ? student : student.id;
    return this.http.delete<Response>(this.studentUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<Response>('deleteStudent'))
    );
  }

  /** PUT: update the hero on the server */
  updateStudent(student: Student): Observable<Response> {
    return this.http.put<Response>(this.studentUrl, student, httpOptions).pipe(
      catchError(this.handleError<Response>('updateStudent'))
    );
  }

  /**
   * Handle Http operation that failed.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error); // log to console instead
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }


}
