import {Component, DoCheck, OnInit} from '@angular/core';
import {Course} from '../../entity';
import {CourseService} from '../../course.service';
import {ActivatedRoute, Router} from '@angular/router';

@Component({
  selector: 'app-course-teacher',
  templateUrl: './course-teacher.component.html',
  styleUrls: ['./course-teacher.component.css']
})
export class CourseTeacherComponent implements OnInit, DoCheck {
  private courses: Course[];
  private dataShow: Course[]; // 分页后在当前页要显示的数据
  public leng: number;                 // 数据总量，需要传给子组件的变量
  public currentPag: number;           // 保存从子组件中传来的当前显示页面
  public pagNums: number;              // 保存从子组件中传来的每页显示数量
  public oldCurrentPag: number;
  public oldPagNums: number;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private courseService: CourseService
  ) {
  }

  ngOnInit() {
    this.getCoursesByTeacher();
  }

  // DoCheck 触发变更检测机制就是调用DoCheck钩子，目的是保证组件属性和浏览器的显示同步
  ngDoCheck(): void {
    if (this.currentPag !== this.oldCurrentPag || this.pagNums !== this.oldPagNums) {
      this.dataShow = [];
      let startNum = (this.currentPag - 1) * this.pagNums;
      for (let i = 0; i < this.pagNums; i++, startNum++) {
        if (this.courses[startNum] !== null && this.courses[startNum] !== undefined) {
          this.dataShow.push(this.courses[startNum]);
        }
      }
      this.oldCurrentPag = this.currentPag;
      this.oldPagNums = this.pagNums;
    }
  }

// 捕获当前显示页码函数
  currentPagHandel(pag: number) {
    this.currentPag = pag;
  }

// 捕获每页显示数量行数
  pagNumsHandel(pagNum: number) {
    this.pagNums = pagNum;
  }

  // 获取所有Course数据
  getCoursesByTeacher(): void {
    // tslint:disable-next-line:variable-name
    const teacher_id = window.localStorage.getItem('teacher_id');
    if (teacher_id === null) {
      alert('请登录或重新登录!');
      this.router.navigateByUrl('/');
    } else {
      this.courseService.getCoursesByTeacher(teacher_id)
        .subscribe(res => {
          this.courses = res;
          this.leng = res.length;
        });
    }
  }

}
