import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable, of} from 'rxjs';
import {Grade, Response} from './entity';
import {catchError} from 'rxjs/operators';

const httpOptions = {
  headers: new HttpHeaders({'Content-Type': 'application/json'})
};

@Injectable({
  providedIn: 'root'
})

export class GradeService {
  response: Response;
  private gradeUrl = '../grade.ctl';

  constructor(private http: HttpClient) {
  }

  /** GET grades from the server */
  getGrades(): Observable<Grade[]> {
    return this.http.get<Grade[]>(this.gradeUrl).pipe(
      catchError(this.handleError<Grade[]>('getGrades', []))
    );
  }

  /** GET grade by id. Will 404 if id not found */
  getGrade(id: number): Observable<Grade> {
    const url = this.gradeUrl + '?id=' + id;
    return this.http.get<Grade>(url).pipe(
      catchError(this.handleError<Grade>(`getGrade id=${id}`))
    );
  }

  /** POST: add a new grade to the server */
  addGrade(grade: Grade): Observable<Response> {
    return this.http.post<Response>(this.gradeUrl, grade, httpOptions).pipe(
      catchError(this.handleError<Response>('addGrade'))
    );
  }

  /** DELETE: detail the grade from the server */
  deleteGrade(grade: Grade | number): Observable<Response> {
    const id = typeof grade === 'number' ? grade : grade.id;
    return this.http.delete<Response>(this.gradeUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<Response>('deleteGrade'))
    );
  }

  /** PUT: update the hero on the server */
  updateGrade(grade: Grade): Observable<Response> {
    return this.http.put<Response>(this.gradeUrl, grade, httpOptions).pipe(
      catchError(this.handleError<Response>('updateGrade'))
    );
  }

  /**
   * Handle Http operation that failed.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error); // log to console instead
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
