import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import {LogoutService} from '../logout.service';

@Component({
  selector: 'app-teacher-user',
  templateUrl: './teacher-user.component.html',
  styleUrls: ['./teacher-user.component.css']
})
export class TeacherUserComponent implements OnInit {
  private teacherUsername = window.localStorage.getItem('teacherUsername');
  // tslint:disable-next-line:variable-name
  private teacher_name = window.localStorage.getItem('teacher_name');
  // tslint:disable-next-line:variable-name
  private teacher_user_id = window.localStorage.getItem('teacher_user_id');
  private teacherLoginTime = window.localStorage.getItem('teacherLoginTime');

  constructor(private router: Router, private logoutService: LogoutService) {
  }

  ngOnInit() {
  }

  logout() {
    // tslint:disable-next-line:variable-name
    const confirm_ = confirm('确认退出?');
    if (confirm_) {
      this.logoutService.logout().subscribe(res => {
        this.router.navigateByUrl('/');
      });
    } else {
      return;
    }
  }
}
