import { Component, OnInit } from '@angular/core';
import {Administrator} from '../../entity';
import {ActivatedRoute} from '@angular/router';
import {Location} from '@angular/common';
import {AdministratorService} from '../../administrator.service';

@Component({
  selector: 'app-update',
  templateUrl: './administrator-update.component.html',
  styleUrls: ['./administrator-update.component.css']
})
export class AdministratorUpdateComponent implements OnInit {
  administrator: Administrator;

  constructor(
    private route: ActivatedRoute,
    private administratorService: AdministratorService,
    private location: Location
  ) {
  }


  ngOnInit() {
    const id = this.route.snapshot.params.id;
    this.administratorService.getAdministrator(id)
      .subscribe(res => this.administrator = res);
  }

  goBack(): void {
    this.location.back();
  }

  save(): void {
    this.administratorService.updateAdministrator(this.administrator)
      .subscribe(res => {alert(res.message); this.goBack(); });
  }
}
