import {Component, OnInit} from '@angular/core';
import {Teacher} from '../../entity';
import {ActivatedRoute, Router} from '@angular/router';
import {TeacherService} from '../../teacher.service';
import {Location} from '@angular/common';

@Component({
  selector: 'app-teacher-person',
  templateUrl: './teacher-person.component.html',
  styleUrls: ['./teacher-person.component.css']
})
export class TeacherPersonComponent implements OnInit {
  teacher: Teacher;
  // tslint:disable-next-line:variable-name
  private teacher_user_id = window.localStorage.getItem('teacher_user_id');

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private teacherService: TeacherService,
    private location: Location
  ) {
  }

  ngOnInit() {
    // tslint:disable-next-line:variable-name
    const teacher_id = window.localStorage.getItem('teacher_id');
    if (teacher_id === null) {
      alert('请登录或重新登录!');
      this.router.navigateByUrl('/');
    } else {
      this.teacherService.getTeacher(teacher_id)
        .subscribe((res) => {
          this.teacher = res;
        });
    }
  }

  goBack(): void {
    this.location.back();
  }

}
