import { Component, OnInit } from '@angular/core';
import {Grade, Department, School, Student} from '../../entity';
import {ActivatedRoute} from '@angular/router';
import {StudentService} from '../../student.service';
import {DepartmentService} from '../../department.service';
import {SchoolService} from '../../school.service';
import {Location} from '@angular/common';
import {GradeService} from '../../grade.service';

@Component({
  selector: 'app-add',
  templateUrl: './student-add.component.html',
  styleUrls: ['./student-add.component.css']
})
export class StudentAddComponent implements OnInit {
  grades: Grade[];
  schools: School[];
  departments: Department[];
  student: Student;
  grade: Grade;
  department: Department;
  school: School;
  constructor(
    private route: ActivatedRoute,
    private studentService: StudentService,
    private gradeService: GradeService,
    private departmentService: DepartmentService,
    private schoolService: SchoolService,
    private location: Location,
  ) { }

  ngOnInit() {
    this.getGrades() ;
    this.getDepartments();
    this.getSchools();
  }
  getSchools(): void {
    this.schoolService.getSchools ()
      .subscribe(res => this.schools = res);
  }
  getGrades(): void {
    this.gradeService.getGrades ()
      .subscribe(res => this.grades = res);
  }
  getDepartments(): void {
    this.departmentService.getDepartments ()
      .subscribe(res => this.departments = res);
  }
  getDepartmentBySchool(id: number): void {
    this.schoolService.getDepartmentBySchool(id)
      .subscribe(res => this.departments = res);
  }
  getGradeByDepartment(id: number): void {
    this.departmentService.getGradeByDepartment(id)
      .subscribe(res => this.grades = res);
  }
  // @ts-ignore
  compareFn(o1: Compare, o2: Compare): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }
  save(no: string,  name: string, phone: string, grade: Grade): void {
    // tslint:disable-next-line:max-line-length
    this.student = {id: null, no: no.trim(), name: name.trim(), phone: phone.trim(), grade: this.grade};
    if (!name || !grade || !no || !phone) { alert('请将信息填写完整!'); return; }
    this.studentService.addStudent(this.student)
      .subscribe(res => {alert(res.message); this.goBack(); });
  }

  goBack(): void {
    this.location.back();
  }

}
