import {Injectable} from '@angular/core';
import {User, Response} from './entity';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable, of} from 'rxjs';
import {catchError} from 'rxjs/operators';

const httpOptions = {
  headers: new HttpHeaders({'Content-Type': 'application/json'})
};

@Injectable({
  providedIn: 'root'
})
export class UserService {
  response: Response;
  private userUrl = '../user.ctl';

  constructor(private http: HttpClient) {
  }

  /*------------------------------------------------------------------------------------------*/
  /*------------------------------------------------------------------------------------------*/

  /** GET user by id. Will 404 if id not found */
  getUserTeacherById(id: number | string): Observable<User> {
    const url = this.userUrl + '?paraType=teacher&id=' + id;
    return this.http.get<User>(url).pipe(
      catchError(this.handleError<User>(`getUserTeacherById id=${id}`))
    );
  }

  /** GET user by id. Will 404 if id not found */
  getUserTeacherByUserName(username: string): Observable<User> {
    const url = this.userUrl + '?paraType=teacher&username=' + username;
    return this.http.get<User>(url).pipe(
      catchError(this.handleError<User>(`getUserByUserName username=${username}`))
    );
  }

  /** PUT: update the hero on the server */
  changeTeacherPassWord(user: User): Observable<Response> {
    return this.http.post<Response>(this.userUrl + '?paraType=teacher', user, httpOptions).pipe(
      catchError(this.handleError<Response>('changeTeacherPassWord'))
    );
  }

  /*------------------------------------------------------------------------------------------*/
  /*------------------------------------------------------------------------------------------*/

  /** GET user by id. Will 404 if id not found */
  getUserAdministratorById(id: number | string): Observable<User> {
    const url = this.userUrl + '?paraType=administrator&id=' + id;
    return this.http.get<User>(url).pipe(
      catchError(this.handleError<User>(`getUserAdministratorById id=${id}`))
    );
  }

  /** GET user by id. Will 404 if id not found */
  getUserAdministratorByUserName(username: string): Observable<User> {
    const url = this.userUrl + '?paraType=administrator&username=' + username;
    return this.http.get<User>(url).pipe(
      catchError(this.handleError<User>(`getUserByUserName username=${username}`))
    );
  }

  /** PUT: update the hero on the server */
  changeAdministratorPassWord(user: User): Observable<Response> {
    return this.http.post<Response>(this.userUrl + '?paraType=administrator', user, httpOptions).pipe(
      catchError(this.handleError<Response>('changeAdministratorPassWord'))
    );
  }

  /*------------------------------------------------------------------------------------------*/
  /*------------------------------------------------------------------------------------------*/

  /** GET user by id. Will 404 if id not found */
  getUserStudentById(id: number | string): Observable<User> {
    const url = this.userUrl + '?paraType=student&id=' + id;
    return this.http.get<User>(url).pipe(
      catchError(this.handleError<User>(`getUserStudentById id=${id}`))
    );
  }

  /** GET user by id. Will 404 if id not found */
  getUserStudentByUserName(username: string): Observable<User> {
    const url = this.userUrl + '?paraType=student&username=' + username;
    return this.http.get<User>(url).pipe(
      catchError(this.handleError<User>(`getUserByUserName username=${username}`))
    );
  }

  /** PUT: update the hero on the server */
  changeStudentPassWord(user: User): Observable<Response> {
    return this.http.post<Response>(this.userUrl + '?paraType=student', user, httpOptions).pipe(
      catchError(this.handleError<Response>('changeStudentPassWord'))
    );
  }

  /*------------------------------------------------------------------------------------------*/
  /*------------------------------------------------------------------------------------------*/

  /**
   * Handle Http operation that failed.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error); // log to console instead
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
