import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {Location} from '@angular/common';
import {AdministratorService} from '../../administrator.service';
import {Administrator} from '../../entity';

@Component({
  selector: 'app-administrator-person',
  templateUrl: './administrator-person.component.html',
  styleUrls: ['./administrator-person.component.css']
})
export class AdministratorPersonComponent implements OnInit {
  administrator: Administrator;
  // tslint:disable-next-line:variable-name
  private administrator_user_id = window.localStorage.getItem('administrator_user_id');

  constructor(
    private route: ActivatedRoute,
    private administratorService: AdministratorService,
    private location: Location
  ) {
  }

  ngOnInit() {
    // tslint:disable-next-line:variable-name
    const administrator_id = window.localStorage.getItem('administrator_id');
    this.administratorService.getAdministrator(administrator_id)
      .subscribe((res) => {
        this.administrator = res;
      });
  }

  goBack(): void {
    this.location.back();
  }

}
