import { Injectable } from '@angular/core';
import {Elective, Response} from './entity';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable, of} from 'rxjs';
import {catchError} from 'rxjs/operators';

const httpOptions = {
  headers: new HttpHeaders({'Content-Type': 'application/json'})
};

@Injectable({
  providedIn: 'root'
})
export class ElectiveService {
  response: Response;
  private electiveUrl = '../elective.ctl';

  constructor(private http: HttpClient) {
  }

  /** GET elective by id. Will 404 if id not found */
  // tslint:disable-next-line:variable-name
  getElectives(student_id: number | string): Observable<Elective[]> {
    const url = this.electiveUrl + '?student_id=' + student_id;
    return this.http.get<Elective[]>(url).pipe(
      catchError(this.handleError<Elective[]>(`getElectives student_id=${student_id}`, []))
    );
  }

  /** GET elective by id. Will 404 if id not found */
  // tslint:disable-next-line:variable-name
  getElectivesByCourse(course_id: number | string): Observable<Elective[]> {
    const url = this.electiveUrl + '?course_id=' + course_id;
    return this.http.get<Elective[]>(url).pipe(
      catchError(this.handleError<Elective[]>(`getElectives course_id=${course_id}`, []))
    );
  }

  /** POST: add a new elective to the server */
  // tslint:disable-next-line:variable-name
  addElective(student_id: number | string, course_id: number | string): Observable<Response> {
    const url = this.electiveUrl + '?student_id=' + student_id + '&course_id=' + course_id;
    return this.http.post<Response>(url, httpOptions).pipe(
      catchError(this.handleError<any>('addElective'))
    );
  }

  /** DELETE: detail the elective from the server */
  deleteElective(elective: Elective | number): Observable<Response> {
    // tslint:disable-next-line:variable-name
    const student_id = typeof elective === 'number' ? elective : elective.student.id;
    // tslint:disable-next-line:variable-name
    const course_id = typeof elective === 'number' ? elective : elective.course.id;
    return this.http.delete<Response>(this.electiveUrl + '?student_id=' + student_id + '&course_id=' + course_id, httpOptions).pipe(
      catchError(this.handleError<Response>('deleteElective'))
    );
  }

  /**
   * Handle Http operation that failed.
   * @param operation - name of the operation that failed
   * @param result - optional value to r+eturn as the observable result
   */
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error); // log to console instead
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
