import {Component, OnInit} from '@angular/core';
import {Student, User} from '../../entity';
import {UserService} from '../../user.service';
import {Location} from '@angular/common';
import {StudentService} from '../../student.service';

@Component({
  selector: 'app-change-student',
  templateUrl: './change-student.component.html',
  styleUrls: ['./change-student.component.css']
})
export class ChangeStudentComponent implements OnInit {
  // tslint:disable-next-line:variable-name
  private student_user_id = window.localStorage.getItem('student_user_id');
  private studentPassword: string;
  user: User;
  student: Student;

  constructor(private userService: UserService,
              private studentService: StudentService,
              private location: Location) {
  }

  ngOnInit() {
    this.userService.getUserStudentById(this.student_user_id)
      .subscribe(res => this.user = res);
    // tslint:disable-next-line:variable-name
    const student_id = window.localStorage.getItem('student_id');
    this.studentService.getStudent(student_id)
      .subscribe(res => this.student = res);
  }

  goBack(): void {
    this.location.back();
  }

  save(oldPassword: string, newPassword: string, renewPassword: string): void {
    this.userService.getUserStudentById(this.student_user_id)
      .subscribe((res) => this.studentPassword = res.password);
    if (!oldPassword || !newPassword || !renewPassword) {
      alert('😮请输入旧密码和新密码!😮');
      return;
    } else if (oldPassword !== this.studentPassword) {
      alert('😮您输入的旧密码不正确!😮');
      return;
    } else if (newPassword !== renewPassword) {
      alert('😮您两次输入的新密码不一致!😮');
      return;
    } else {
      this.user.password = renewPassword;
      this.userService.changeStudentPassWord(this.user)
        .subscribe(res => {
          alert(res.message);
          this.goBack();
        });
    }
  }

  save2(): void {
    this.studentService.updateStudent(this.student)
      .subscribe(res => {
        alert(res.message);
        this.goBack();
      });
  }
}
