import {Component, OnInit} from '@angular/core';
import {Course} from '../entity';
import {CourseService} from '../course.service';
import {ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-charts',
  templateUrl: './charts.component.html',
  styleUrls: ['./charts.component.css']
})
export class ChartsComponent implements OnInit {
  private courses: Course[];

  constructor(
    private route: ActivatedRoute,
    private courseService: CourseService
  ) {
  }

  ngOnInit() {
    this.getCourses();
  }

  // 获取所有Course数据
  getCourses(): void {
    this.courseService.getCourses()
      .subscribe(res => {
        this.courses = res;
        const newCourses = [];
        const countNum = [];
        const credits = [];
        for (const course of this.courses) {
          newCourses.push(course.description);
          countNum.push(course.countNum);
          credits.push(course.credit);
        }
        const Highcharts = require('highcharts');
        require('highcharts/modules/exporting')(Highcharts);
        // 创建图表
        Highcharts.chart('container', {
          chart: {
            type: 'line'
          },
          title: {
            text: '各课程学生人数统计'
          },
          subtitle: {
            text: '来源： 学生选课情况'
          },
          xAxis: {
            categories: newCourses,
            crosshair: true
          },
          yAxis: {
            min: 0,
            title: {
              text: '学生人数（人）'
            }
          },
          tooltip: {
            headerFormat: '<span style="font-size:10px">{point.key}</span><table>',
            pointFormat: '<tr><td style="color:{series.color};padding:0">{series.name}: </td>' +
              '<td style="padding:0"><b>{point.y:.1f} 人</b></td></tr>',
            footerFormat: '</table>',
            shared: true,
            useHTML: true
          },
          plotOptions: {
            column: {
              pointPadding: 0.2,
              borderWidth: 0
            }
          },
          credits: {
            enabled: false
          },
          series: [{
            name: '学生人数',
            data: countNum
          }]
        });

        const Highcharts2 = require('highcharts');
        require('highcharts/modules/exporting')(Highcharts2);
        // 创建图表
        Highcharts2.chart('container2', {
          chart: {
            type: 'bar'
          },
          title: {
            text: '各课程学分统计'
          },
          subtitle: {
            text: '来源： 选课系统'
          },
          xAxis: {
            categories: newCourses,
            crosshair: true
          },
          yAxis: {
            min: 0,
            title: {
              text: '学分（分）'
            }
          },
          tooltip: {
            headerFormat: '<span style="font-size:10px">{point.key}</span><table>',
            pointFormat: '<tr><td style="color:{series.color};padding:0">{series.name}: </td>' +
              '<td style="padding:0"><b>{point.y:.1f} 分</b></td></tr>',
            footerFormat: '</table>',
            shared: true,
            useHTML: true
          },
          plotOptions: {
            column: {
              pointPadding: 0.2,
              borderWidth: 0
            }
          },
          credits: {
            enabled: false
          },
          series: [{
            name: '学分',
            data: credits
          }]
        });
      });
  }

}
