import {Component, DoCheck, OnInit} from '@angular/core';
import {Course, Student} from '../../entity';
import {ActivatedRoute} from '@angular/router';
import {CourseService} from '../../course.service';
import {ElectiveService} from '../../elective.service';

@Component({
  selector: 'app-elective',
  templateUrl: './elective.component.html',
  styleUrls: ['./elective.component.css']
})
export class ElectiveComponent implements OnInit, DoCheck {
  private courses: Course[];
  private dataShow: Course[]; // 分页后在当前页要显示的数据
  public leng: number;                 // 数据总量，需要传给子组件的变量
  public currentPag: number;           // 保存从子组件中传来的当前显示页面
  public pagNums: number;              // 保存从子组件中传来的每页显示数量
  public oldCurrentPag: number;
  public oldPagNums: number;
  private desp: string;

  constructor(
    private route: ActivatedRoute,
    private courseService: CourseService,
    private electiveService: ElectiveService
  ) {
  }

  ngOnInit() {
    this.getCourses();
  }

  // DoCheck 触发变更检测机制就是调用DoCheck钩子，目的是保证组件属性和浏览器的显示同步
  ngDoCheck(): void {
    if (this.currentPag !== this.oldCurrentPag || this.pagNums !== this.oldPagNums) {
      this.dataShow = [];
      let startNum = (this.currentPag - 1) * this.pagNums;
      for (let i = 0; i < this.pagNums; i++, startNum++) {
        if (this.courses[startNum] !== null && this.courses[startNum] !== undefined) {
          this.dataShow.push(this.courses[startNum]);
        }
      }
      this.oldCurrentPag = this.currentPag;
      this.oldPagNums = this.pagNums;
    }
  }

// 捕获当前显示页码函数
  currentPagHandel(pag: number) {
    this.currentPag = pag;
  }

// 捕获每页显示数量行数
  pagNumsHandel(pagNum: number) {
    this.pagNums = pagNum;
  }

  // 获取所有Course数据
  getCourses(): void {
    this.courseService.getCourses()
      .subscribe(res => {
        this.courses = res;
        this.leng = res.length;
        if (this.leng === 0) {
          alert('未到指定选课时间!');
        }
      });
  }

  getCoursesByDes(des: string): void {
    if (!des) {
      alert('请填入查询信息');
    } else {
      this.desp = des;
      this.courseService.getCoursesByDes(this.desp.trim())
        .subscribe(res => {
          this.courses = res;
          if (this.courses[0] !== undefined) {
            this.dataShow = [];
            this.dataShow = res;
            this.currentPag = 1;
          } else {
            alert('未查到相应课程!');
          }
          this.leng = res.length;
        });
    }
  }

  fresh(): void {
    window.location.reload();
  }

  // tslint:disable-next-line:variable-name
  addElective(course_id: number): void {
    // tslint:disable-next-line:variable-name
    const confirm_ = confirm('确认选中?');
    if (confirm_) {
      // tslint:disable-next-line:variable-name
      const student_id = window.localStorage.getItem('student_id');
      this.electiveService.addElective(student_id, course_id).subscribe(res => {
        alert(res.message);
      });
    } else {
      return;
    }
  }
}
