import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';

import {TeacherComponent} from './teacher/teacher.component';
import {TeacherAddComponent} from './teacher/add/teacher-add.component';
import {TeacherUpdateComponent} from './teacher/update/teacher-update.component';

import {LoginComponent} from './login/login.component';
import {AdministratorUserComponent} from './administrator-user/administrator-user.component';
import {StudentComponent} from './student/student.component';
import {StudentAddComponent} from './student/add/student-add.component';
import {CourseComponent} from './course/course.component';
import {CourseAddComponent} from './course/add/course-add.component';
import {CourseUpdateComponent} from './course/update/course-update.component';
import {AdministratorPersonComponent} from './administrator-user/administrator-person/administrator-person.component';
import {ChangeAdministratorComponent} from './administrator-user/change-administrator/change-administrator.component';
import {StudentUserComponent} from './student-user/student-user.component';
import {StudentPersonComponent} from './student-user/student-person/student-person.component';
import {ChangeStudentComponent} from './student-user/change-student/change-student.component';
import {ElectiveComponent} from './student-user/elective/elective.component';
import {CheckComponent} from './student-user/check/check.component';
import {TeacherUserComponent} from './teacher-user/teacher-user.component';
import {ChangeTeacherComponent} from './teacher-user/change-teacher/change-teacher.component';
import {TeacherPersonComponent} from './teacher-user/teacher-person/teacher-person.component';
import {CourseTeacherComponent} from './teacher-user/course-teacher/course-teacher.component';
import {StudentUpdateComponent} from './student/update/student-update.component';
import {ListStudentComponent} from './teacher-user/list-student/list-student.component';
import {ListStudentAdComponent} from './administrator-user/list-student-ad/list-student-ad.component';
import {AdministratorComponent} from './administrator/administrator.component';
import {AdministratorAddComponent} from './administrator/add/administrator-add.component';
import {AdministratorUpdateComponent} from './administrator/update/administrator-update.component';
import {ChartsComponent} from './charts/charts.component';

const routes: Routes = [
  {path: '', component: LoginComponent, pathMatch: 'full'}, // 默认路由(登录)

  {
    path: 'administratorIndex', component: AdministratorUserComponent, children: [ // 嵌套路由
      {path: '', redirectTo: '/course', pathMatch: 'full'},
      {path: 'administratorPerson', component: AdministratorPersonComponent},
      {path: 'changeAdministrator/:id', component: ChangeAdministratorComponent},
      {path: 'student', component: StudentComponent},
      {path: 'student-add', component: StudentAddComponent},
      {path: 'student-update/:id', component: StudentUpdateComponent},
      {path: 'course', component: CourseComponent},
      {path: 'course-add', component: CourseAddComponent},
      {path: 'listStudentAd/:id', component: ListStudentAdComponent},
      {path: 'course-update/:id', component: CourseUpdateComponent},
      {path: 'teacher', component: TeacherComponent},
      {path: 'teacher-add', component: TeacherAddComponent},
      {path: 'teacher-update/:id', component: TeacherUpdateComponent},
      {path: 'administrator', component: AdministratorComponent},
      {path: 'administrator-add', component: AdministratorAddComponent},
      {path: 'administrator-update/:id', component: AdministratorUpdateComponent},
      {path: 'charts', component: ChartsComponent}
    ]
  },

  {
    path: 'studentIndex', component: StudentUserComponent, children: [ // 嵌套路由
      {path: '', redirectTo: '/check', pathMatch: 'full'},
      {path: 'studentPerson', component: StudentPersonComponent},
      {path: 'changeStudent/:id', component: ChangeStudentComponent},
      {path: 'elective', component: ElectiveComponent},
      {path: 'check', component: CheckComponent}
    ]
  },

  {
    path: 'teacherIndex', component: TeacherUserComponent, children: [ // 嵌套路由
      {path: '', redirectTo: '/courseTeacher', pathMatch: 'full'},
      {path: 'teacherPerson', component: TeacherPersonComponent},
      {path: 'changeTeacher/:id', component: ChangeTeacherComponent},
      {path: 'listStudent/:id', component: ListStudentComponent},
      {path: 'courseTeacher', component: CourseTeacherComponent}
    ]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {
}

